import { __ } from '@wordpress/i18n';
import "./editor.scss";

import Inspector from "./components/inspector";
import { plus, lineSolid } from "@wordpress/icons";
import { useBlockProps, BlockControls } from "@wordpress/block-editor";
import { usePreviewMode } from './components/usePreviewMode';
import { useFormData } from './components/useFormData';
import { useFormEventListener } from './components/useFormEventListener';

import ServerSideRender from "@wordpress/server-side-render";

const { Fragment, useEffect } = wp.element;
const { SelectControl, ToolbarButton, ToolbarGroup, Tooltip } = wp.components;

export default function Edit(props) {
  const { attributes, setAttributes } = props;
  const { form_selector_toggle } = attributes;
  const { formOptions, selectForm } = useFormData(props);

  usePreviewMode(setAttributes);

  useEffect(useFormEventListener, []);

  return (
    <div {...useBlockProps()}>
      <Fragment>
        <BlockControls>
          <ToolbarGroup>
            <Tooltip text={__("Toggle contact form selector", "customizer-block-cf7")}>
              <ToolbarButton
                icon={form_selector_toggle === false ? plus : lineSolid}
                onClick={() => setAttributes({ form_selector_toggle: !form_selector_toggle })}
              />
            </Tooltip>
          </ToolbarGroup>
        </BlockControls>

        {form_selector_toggle && (
          <div>
            <SelectControl
              value={attributes.contact_form}
              options={formOptions}
              onChange={selectForm}
              __nextHasNoMarginBottom
            />
          </div>
        )}

        <ServerSideRender
            block="mofistudio/customizer-block-cf7"
            attributes={attributes}
        />

        <Inspector {...{ setAttributes, ...props }} />
      </Fragment>
    </div>
  );
}
