import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import { RangeControl, PanelBody } from "@wordpress/components";

export default class SubmitPadding extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { submit_padding_top_bottom, submit_padding_left_right },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Submit Padding", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-submit-padding'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-submit-padding')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Top & Bottom", "customizer-block-cf7")}
            value={submit_padding_top_bottom}
            onChange={(submit_padding_top_bottom) =>
              setAttributes({ submit_padding_top_bottom })
            }
            step={0.1}
            min={0}
            max={2}
          />

          <RangeControl
            label={__("Left & Right", "customizer-block-cf7")}
            value={submit_padding_left_right}
            onChange={(submit_padding_left_right) =>
              setAttributes({ submit_padding_left_right })
            }
            step={0.1}
            min={0}
            max={10}
          />
        </div>
      </PanelBody>
    );
  }
}
