import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import { PanelBody, RangeControl } from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class FieldText extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { field_font_size, field_text_color, placeholder_text_color },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;

    return (
      <PanelBody
        title={__("Field Text", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-text'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-text')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Font Size", "customizer-block-cf7")}
            value={field_font_size}
            onChange={(field_font_size) => setAttributes({ field_font_size })}
            step={SIZE_STEP}
            min={0.9}
            max={1.6}
          />

          <PanelColorSettings
            initialOpen={false}
            className="cfcf7-colorpanel-spacer"
            title={__("Field Text Color", "customizer-block-cf7")}
            colorSettings={[
              {
                value: field_text_color,
                onChange: (field_text_color) =>
                  setAttributes({ field_text_color }),
                label: "",
              },
            ]}
          />

          <PanelColorSettings
            initialOpen={true}
            className="cfcf7-colorpanel-spacer"
            title={__("Placeholder Text Color", "customizer-block-cf7")}
            colorSettings={[
              {
                value: placeholder_text_color,
                onChange: (placeholder_text_color) =>
                  setAttributes({ placeholder_text_color }),
                label: "",
              },
            ]}
          />
        </div>
      </PanelBody>
    );
  }
}
