import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import PreviewIconDesktop from "./previewicondesktop";
import PreviewIconTablet from "./previewicontablet";
import PreviewIconMobile from "./previewiconmobile";

import { RangeControl, PanelBody } from "@wordpress/components";

export default class BlockPadding extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        padding_top,
        padding_sides,
        padding_bottom,
        padding_top_tablet,
        padding_sides_tablet,
        padding_bottom_tablet,
        padding_top_mobile,
        padding_sides_mobile,
        padding_bottom_mobile,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;
    const SIZE_MAX = 5;

    const isPrewiewMode = this.props.attributes.previewmode;

    if (isPrewiewMode === "Desktop") {
      return (
        <PanelBody
          title={__("Block Padding", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-padding-desktop'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-padding-desktop')} // Handle child panel toggle
        >
          <PreviewIconDesktop />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={padding_top}
              onChange={(padding_top) => setAttributes({ padding_top })}
              step={SIZE_STEP}
              min={1}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={padding_sides}
              onChange={(padding_sides) => setAttributes({ padding_sides })}
              step={SIZE_STEP}
              min={0.5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={padding_bottom}
              onChange={(padding_bottom) => setAttributes({ padding_bottom })}
              step={SIZE_STEP}
              min={1}
              max={SIZE_MAX}
            />
          </div>
        </PanelBody>
      );
    } else if (isPrewiewMode === "Tablet") {
      return (
        <PanelBody
          title={__("Block Padding", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-padding-tablet'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-padding-tablet')} // Handle child panel toggle
        >
          <PreviewIconTablet />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={padding_top_tablet}
              onChange={(padding_top_tablet) => setAttributes({ padding_top_tablet })}
              step={SIZE_STEP}
              min={1}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={padding_sides_tablet}
              onChange={(padding_sides_tablet) => setAttributes({ padding_sides_tablet })}
              step={SIZE_STEP}
              min={0.5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={padding_bottom_tablet}
              onChange={(padding_bottom_tablet) => setAttributes({ padding_bottom_tablet })}
              step={SIZE_STEP}
              min={1}
              max={SIZE_MAX}
            />
          </div>
        </PanelBody>
      );
    } else {
      return (
        <PanelBody
          title={__("Block Padding", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-padding-mobile'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-padding-mobile')} // Handle child panel toggle
        >
          <PreviewIconMobile />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={padding_top_mobile}
              onChange={(padding_top_mobile) => setAttributes({ padding_top_mobile })}
              step={SIZE_STEP}
              min={0.1}
              max={3}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={padding_sides_mobile}
              onChange={(padding_sides_mobile) => setAttributes({ padding_sides_mobile })}
              step={SIZE_STEP}
              min={0.1}
              max={3}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={padding_bottom_mobile}
              onChange={(padding_bottom_mobile) => setAttributes({ padding_bottom_mobile })}
              step={SIZE_STEP}
              min={0.1}
              max={3}
            />
          </div>
        </PanelBody>
      );
    }
  }
}
