import { __ } from '@wordpress/i18n';
const { Component } = wp.element;

import { PanelColorSettings } from "@wordpress/block-editor";
import { PanelBody, RangeControl, ToggleControl } from "@wordpress/components";

export default class BlockBorder extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        top_left_corner,
        top_right_corner,
        bottom_left_corner,
        bottom_right_corner,
        show_block_border,
        block_border_radius,
        block_border_width,
        block_border_color,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;
    const SIZE_MAX = 10;

    return (
      <PanelBody
        title={__("Block Border", "customizer-block-cf7")}
        className="cfcf7-child-panel"
        initialOpen={false}
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-block-border'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-block-border')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Border Radius", "customizer-block-cf7")}
            value={block_border_radius}
            onChange={(block_border_radius) =>
              setAttributes({ block_border_radius })
            }
            step={SIZE_STEP}
            min={0}
            max={SIZE_MAX}
          />

          <ToggleControl
            label={__("Add Border Outline", "customizer-block-cf7")}
            checked={show_block_border}
            onChange={(show_block_border) =>
              setAttributes({ show_block_border })
            }
            className="cfcf7-toggle-spacer"
          />
        </div>
        {show_block_border && (
          <div className="cfcf7-panel-spacer-two">
            <RangeControl
              label={__("Border Width", "customizer-block-cf7")}
              value={block_border_width}
              onChange={(block_border_width) =>
                setAttributes({ block_border_width })
              }
              step={0.1}
              min={0}
              max={2}
            />

            <PanelColorSettings
              title={__("Border Color", "customizer-block-cf7")}
              className="cfcf7-colorpanel-spacer"
              initialOpen={true}
              colorSettings={[
                {
                  value: block_border_color,
                  onChange: (block_border_color) =>
                    setAttributes({ block_border_color }),
                  label: "",
                },
              ]}
            />
          </div>
        )}
      </PanelBody>
    );
  }
}
