import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import {
  PanelBody,
  ToggleControl,
  RangeControl,
  SelectControl
} from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class FieldBorder extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        show_field_border,
        field_border_width,
        field_border_style,
        field_border_color,
        field_border_sides,
        field_border_radius,
        show_field_focus,
        field_focus_outline_width,
        field_focus_outline_offset,
        field_focus_outline_color,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Field Border", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-border'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-border')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Border Radius", "customizer-block-cf7")}
            value={field_border_radius}
            onChange={(field_border_radius) =>
              setAttributes({ field_border_radius })
            }
            step={0.1}
            min={0}
            max={2}
          />

          <ToggleControl
            className="cfcf7-toggle-spacer"
            label={__("Show Field Border", "customizer-block-cf7")}
            checked={show_field_border}
            onChange={(show_field_border) =>
              setAttributes({ show_field_border })
            }
          />

          {show_field_border && (
            <div className="cfcf7-panel-spacer">
              <RangeControl
                label={__("Border Width", "customizer-block-cf7")}
                step={1}
                min={1}
                max={3}
                withInputField={true}
                isShiftStepEnabled
                value={field_border_width}
                onChange={(field_border_width) =>
                  setAttributes({ field_border_width })
                }
              />

              <SelectControl
                className="cfcf7-selectcontrol-spacer"
                label={__("Border Style", "customizer-block-cf7")}
                value={field_border_style}
                options={[
                  { label: "Solid", value: "solid" },
                  { label: "Dashed", value: "dashed" },
                  { label: "Dotted", value: "dotted" },
                ]}
                onChange={(newStyle) => setAttributes({ field_border_style: newStyle })}
              />

              <SelectControl
                className="cfcf7-selectcontrol-spacer"
                label={__("Border Position", "customizer-block-cf7")}
                value={field_border_sides}
                options={[
                  { label: "All Sides", value: "all" },
                  { label: "Bottom Side", value: "bottom" },
                ]}
                onChange={(newPosition) => setAttributes({ field_border_sides: newPosition })}
              />

              <PanelColorSettings
                title={__("Border Color", "customizer-block-cf7")}
                className="cfcf7-colorpanel-spacer"
                initialOpen={true}
                colorSettings={[
                  {
                    value: field_border_color,
                    onChange: (field_border_color) =>
                      setAttributes({ field_border_color }),
                    label: "",
                  },
                ]}
              />
            </div>
          )}

          <ToggleControl
            className="cfcf7-toggle-spacer-two"
            label={__("Field Focus Outline", "customizer-block-cf7")}
            checked={show_field_focus}
            onChange={(show_field_focus) =>
              setAttributes({ show_field_focus })
            }
            help={__("Override browser default input focus styles.", "customizer-block-cf7")}
          />

          {show_field_focus && (
            <div className="cfcf7-panel-spacer">
              <RangeControl
                label={__("Focus Outline Width", "customizer-block-cf7")}
                step={0.1}
                min={1}
                max={5}
                withInputField={true}
                isShiftStepEnabled
                value={field_focus_outline_width}
                onChange={(field_focus_outline_width) =>
                  setAttributes({ field_focus_outline_width })
                }
              />

              <RangeControl
                label={__("Focus Outline Offset", "customizer-block-cf7")}
                step={0.5}
                min={0}
                max={3}
                withInputField={true}
                isShiftStepEnabled
                value={field_focus_outline_offset}
                onChange={(field_focus_outline_offset) =>
                  setAttributes({ field_focus_outline_offset })}
              />

              <PanelColorSettings
                title={__("Focus Outline Color", "customizer-block-cf7")}
                className="cfcf7-colorpanel-spacer"
                initialOpen={true}
                colorSettings={[
                  {
                    value: field_focus_outline_color,
                    onChange: (field_focus_outline_color) =>
                      setAttributes({ field_focus_outline_color }),
                    label: "",
                  },
                ]}
              />
            </div>
          )}
        </div>
      </PanelBody>
    );
  }
}
