import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import {
  PanelBody,
  RangeControl,
  SelectControl
} from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class SubmitText extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        submit_font_size,
        submit_font_case,
        submit_text_color,
        submit_font_weight,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Submit Text", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-submit-text'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-submit-text')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Font Size", "customizer-block-cf7")}
            value={submit_font_size}
            onChange={(submit_font_size) => setAttributes({ submit_font_size })}
            step={0.1}
            min={0.5}
            max={2}
          />
          
         <SelectControl
          className="cfcf7-selectcontrol-spacer"
          label={__("Text transform", "customizer-block-cf7")}
          value={submit_font_case}
          options={[
            { label: __("None", "customizer-block-cf7"), value: "" },
            { label: __("Uppercase", "customizer-block-cf7"), value: "uppercase" },
            { label: __("Lowercase", "customizer-block-cf7"), value: "lowercase" },
            { label: __("Capitalize", "customizer-block-cf7"), value: "capitalize" }
          ]}
          onChange={(newLabelCase) => setAttributes({ submit_font_case: newLabelCase })}
        />

          <SelectControl
            className="cfcf7-selectcontrol-spacer"
            label={__("Font Weight", "customizer-block-cf7")}
            value={submit_font_weight}
            options={[
                { label: __("Thin (100)", "customizer-block-cf7"), value: "100" },
                { label: __("Normal (400)", "customizer-block-cf7"), value: "400" },
                { label: __("Bold (700)", "customizer-block-cf7"), value: "700" },
                { label: __("Heavy (900)", "customizer-block-cf7"), value: "900" },
            ]}
            onChange={(newFontWeight) => setAttributes({ submit_font_weight: newFontWeight })}
        />

          <PanelColorSettings
            title={__("Submit Text Color", "customizer-block-cf7")}
            className="cfcf7-colorpanel-spacer"
            colorSettings={[
              {
                value: submit_text_color,
                onChange: (submit_text_color) =>
                  setAttributes({ submit_text_color }),
                label: "",
              },
            ]}
          />
        </div>
      </PanelBody>
    );
  }
}
