import { useEffect } from '@wordpress/element';
import { useSelect } from '@wordpress/data';

export function usePreviewMode(setAttributes) {
    const deviceType = useSelect((select) => {
        // First, try accessing the newer core/editor getDeviceType if available
        if (select("core/editor")?.getDeviceType) {
            return select("core/editor").getDeviceType();
        }
        // Fallback to the experimental function in core/edit-post if the first is not available
        else if (select("core/edit-post")?.__experimentalGetPreviewDeviceType) {
            return select("core/edit-post").__experimentalGetPreviewDeviceType();
        }
        // Return null if none are available
        return null;
    }, []);

    useEffect(() => {
        if (deviceType) {
            setAttributes({ previewmode: deviceType });
        } else {
            console.log('Device type is not available.');
        }
    }, [deviceType, setAttributes]);
}


