import { __ } from '@wordpress/i18n';

// Function to attach event listener
export function useFormEventListener() {
  // Attach the event listener on mount and clean up on unmount
  document.addEventListener('click', handleFormClick, false);

  // Cleanup function to remove event listener
  return () => {
    document.removeEventListener('click', handleFormClick, false);
  };
}

// Handler for the click event
function handleFormClick(event) {
  if (!event.target.matches('.wpcf7-submit')) return;
  event.preventDefault();
  alert(__("Your form can only be submitted in the front end", "customizer-block-cf7"));
}
