import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import PreviewIconDesktop from "./previewicondesktop";
import PreviewIconTablet from "./previewicontablet";
import PreviewIconMobile from "./previewiconmobile";

import { RangeControl, PanelBody } from "@wordpress/components";

export default class BlockMargin extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        margin_top,
        margin_sides,
        margin_bottom,
        margin_top_tablet,
        margin_sides_tablet,
        margin_bottom_tablet,
        margin_top_mobile,
        margin_sides_mobile,
        margin_bottom_mobile,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;
    const SIZE_MAX = 10;

    const isPrewiewMode = this.props.attributes.previewmode;

    if (isPrewiewMode === "Desktop") {
      return (
        <PanelBody
          title={__("Block Margin", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-margin-desktop'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-margin-desktop')} // Handle child panel toggle
        >
          <PreviewIconDesktop />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={margin_top}
              onChange={(margin_top) => setAttributes({ margin_top })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={margin_sides}
              onChange={(margin_sides) => setAttributes({ margin_sides })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={margin_bottom}
              onChange={(margin_bottom) => setAttributes({ margin_bottom })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />
          </div>
        </PanelBody>
      );
    } else if (isPrewiewMode === "Tablet") {
      return (
        <PanelBody
          title={__("Block Margin", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-margin-tablet'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-margin-tablet')} // Handle child panel toggle
        >
          <PreviewIconTablet />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={margin_top_tablet}
              onChange={(margin_top_tablet) => setAttributes({ margin_top_tablet })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={margin_sides_tablet}
              onChange={(margin_sides_tablet) => setAttributes({ margin_sides_tablet })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={margin_bottom_tablet}
              onChange={(margin_bottom_tablet) => setAttributes({ margin_bottom_tablet })}
              step={SIZE_STEP}
              min={-5}
              max={SIZE_MAX}
            />
          </div>
        </PanelBody>
      );
    } else {
      return (
        <PanelBody
          title={__("Block Margin", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-margin-mobile'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-margin-mobile')} // Handle child panel toggle
        >
          <PreviewIconMobile />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Top", "customizer-block-cf7")}
              value={margin_top_mobile}
              onChange={(margin_top_mobile) => setAttributes({ margin_top_mobile })}
              step={SIZE_STEP}
              min={-5}
              max={6}
            />

            <RangeControl
              label={__("Left & Right", "customizer-block-cf7")}
              value={margin_sides_mobile}
              onChange={(margin_sides_mobile) => setAttributes({ margin_sides_mobile })}
              step={SIZE_STEP}
              min={-5}
              max={3}
            />

            <RangeControl
              label={__("Bottom", "customizer-block-cf7")}
              value={margin_bottom_mobile}
              onChange={(margin_bottom_mobile) => setAttributes({ margin_bottom_mobile })}
              step={SIZE_STEP}
              min={-5}
              max={6}
            />
          </div>
        </PanelBody>
      );
    }
  }
}
