import { __ } from '@wordpress/i18n';
import { PanelColorSettings } from "@wordpress/block-editor";
import { PanelBody } from "@wordpress/components";

export default function BlockBackground({ attributes, setAttributes, openChildPanel, handleChildToggle }) {
  const { block_bg_color } = attributes;

  return (
    <PanelBody
      title={__("Block Background", "customizer-block-cf7")}
      initialOpen={false}
      className="cfcf7-child-panel"
      scrollAfterOpen={true}
      opened={openChildPanel === 'panel-block-background'} // Check if this child panel is open
      onToggle={() => handleChildToggle('panel-block-background')} // Toggle child panel
    >
      <PanelColorSettings
        title={__("Background Color", "customizer-block-cf7")}
        className="cfcf7-colorpanel-spacer"
        initialOpen={true}
        colorSettings={[
          {
            value: block_bg_color,
            onChange: (block_bg_color) => setAttributes({ block_bg_color }),
            label: "",
          },
        ]}
      />
    </PanelBody>
  );
}
