import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import { PanelBody } from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class FieldBackground extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { field_bg_color },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Field Background", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-background'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-background')} // Handle child panel toggle
      >
        <PanelColorSettings
          className="cfcf7-colorpanel-spacer"
          initialOpen={true}
          title={__("Field Background Color", "customizer-block-cf7")}
          colorSettings={[
            {
              value: field_bg_color,
              onChange: (field_bg_color) => setAttributes({ field_bg_color }),
              label: "",
            },
          ]}
        />
      </PanelBody>
    );
  }
}
