import { __ } from '@wordpress/i18n';
const { Component } = wp.element;

import PreviewIconDesktop from "./previewicondesktop";
import PreviewIconTablet from "./previewicontablet";
import PreviewIconMobile from "./previewiconmobile";

import {
  PanelBody,
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from "@wordpress/components";

export default class SubmitWidth extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { 
        submit_width,
        submit_width_tablet,
        submit_width_mobile
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    var isPrewiewMode = this.props.attributes.previewmode;

    if(isPrewiewMode == "Desktop") {
      return (
        <PanelBody
          title={__("Submit Width", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-submit-width-desktop'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-submit-width-desktop')} // Handle child panel toggle
        >
          <PreviewIconDesktop />
          <div className="cfcf7-panel-spacer">
            <ToggleGroupControl value={submit_width} isBlock
              onChange={(submit_width) => {
                setAttributes({ submit_width });
              }}
            >
              <ToggleGroupControlOption value="50" label="Auto" />
              <ToggleGroupControlOption value="100" label="100%" />
            </ToggleGroupControl>
          </div>
        </PanelBody>
      );
    }
    else if(isPrewiewMode == "Tablet"){
      return (
        <PanelBody
          title={__("Submit Width", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-submit-width-tablet'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-submit-width-tablet')} // Handle child panel toggle
        >
          <PreviewIconTablet />
          <div className="cfcf7-panel-spacer">
            <ToggleGroupControl value={submit_width_tablet} isBlock
              onChange={(submit_width_tablet) => {
                setAttributes({ submit_width_tablet });
              }}
            >
              <ToggleGroupControlOption value="50" label="Auto" />
              <ToggleGroupControlOption value="100" label="100%" />
            </ToggleGroupControl>
          </div>
        </PanelBody>
      );
    }
    else {
      return (
        <PanelBody
          title={__("Submit Width", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-submit-width-mobile'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-submit-width-mobile')} // Handle child panel toggle
        >
          <PreviewIconMobile />
          <div className="cfcf7-panel-spacer">
            <ToggleGroupControl value={submit_width_mobile} isBlock
              onChange={(submit_width_mobile) => {
                setAttributes({ submit_width_mobile });
              }}
            >
              <ToggleGroupControlOption value="50" label="Auto" />
              <ToggleGroupControlOption value="100" label="100%" />
            </ToggleGroupControl>
          </div>
        </PanelBody>
      );
    }
  }
}
