import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import {
  RangeControl,
  PanelBody,
} from "@wordpress/components";

export default class FieldPadding extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { field_padding_top_bottom, field_padding_left_right },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;

    return (
      <PanelBody
        title={__("Field Padding", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-padding'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-padding')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Top & Bottom", "customizer-block-cf7")}
            value={field_padding_top_bottom}
            onChange={(field_padding_top_bottom) =>
              setAttributes({ field_padding_top_bottom })
            }
            step={SIZE_STEP}
            min={0.4}
            max={1}
          />

          <RangeControl
            label={__("Left & Right", "customizer-block-cf7")}
            value={field_padding_left_right}
            onChange={(field_padding_left_right) =>
              setAttributes({ field_padding_left_right })
            }
            step={SIZE_STEP}
            min={0.4}
            max={1}
          />
        </div>
      </PanelBody>
    );
  }
}
