import { __ } from '@wordpress/i18n';
import { Component } from '@wordpress/element';
import {
  PanelBody,
  GradientPicker,
  ToggleControl,
} from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class SubmitBackground extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { 
        submit_bg_color,
        show_gradient_picker,
        submit_bg_gradient
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const onChangeGradient = (newGradient) => {
      setAttributes({ submit_bg_gradient: newGradient });
    };

    return (
      <PanelBody
        title={__("Submit Background", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-submit-background'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-submit-background')} // Handle child panel toggle
      >
        {!show_gradient_picker && (
          <PanelColorSettings
            title={__("Submit Background Color", "customizer-block-cf7")}
            initialOpen={true}
            className="cfcf7-submit-colorpanel"
            colorSettings={[
              {
                value: submit_bg_color,
                onChange: (submit_bg_color) => setAttributes({ submit_bg_color }),
                label: "",
              },
            ]}
          />
        )}

        <div className="cfcf7-panel-spacer">
          <ToggleControl
            label={
              show_gradient_picker
                ? __("Remove Gradient Background", "customizer-block-cf7")
                : __("Add Gradient Background", "customizer-block-cf7")
            }
            checked={show_gradient_picker}
            onChange={(show_gradient_picker) =>
              setAttributes({ show_gradient_picker })
            }
            className="cfcf7-toggle-spacer"
          />

          {show_gradient_picker && (
            <GradientPicker
              value={submit_bg_gradient}
              onChange={onChangeGradient}
              asButtons={true}
              gradients={[
                {
                  name: 'Peaches',
                  gradient: 'linear-gradient(130deg,#ff897b 0%, #ffb364 100%)',
                  slug: 'peaches',
                },
                {
                  name: 'Rana',
                  gradient: 'linear-gradient(42deg,#A0F0C9 0%, #CEF2C6 100%)',
                  slug: 'rana',
                },
                {
                  name: 'Parma Violet',
                  gradient: 'linear-gradient(200deg, #F7C3F7 0%, #E4AAF1 100%)',
                  slug: 'parma-violet',
                },
                {
                  name: 'Red Sky',
                  gradient: 'linear-gradient(44deg, #E97585 0%, #F64CA3 100%)',
                  slug: 'berry-nice',
                },
                {
                  name: 'Blackcurrant',
                  gradient: 'linear-gradient(180deg, #921E8F, #2F0A2F)',
                  slug: 'blackcurrant',
                },
                {
                  name: 'Tru Blu',
                  gradient: 'linear-gradient(191deg, #23a9f2, #1D88C2)',
                  slug: 'tru-blu',
                },
              ]}
            />
          )}
        </div>
      </PanelBody>
    );
  }
}
