import { __ } from '@wordpress/i18n';
import { PanelBody, RangeControl, ToggleControl } from "@wordpress/components";
import { PanelColorSettings } from "@wordpress/block-editor";

const BlockShadow = ({ attributes, setAttributes, openChildPanel, handleChildToggle }) => {
  const {
    show_box_shadow,
    block_h_offset,
    block_v_offset,
    block_blur,
    block_spread,
    block_shadow_color,
  } = attributes;

  return (
    <PanelBody
      title={__("Block Shadow", "customizer-block-cf7")}
      initialOpen={false}
      className="cfcf7-child-panel"
      scrollAfterOpen={true}
      opened={openChildPanel === 'panel-block-shadow'} // Check if this child panel is open
      onToggle={() => handleChildToggle('panel-block-shadow')} // Handle child panel toggle
    >
      <div className="cfcf7-panel-spacer">
        <ToggleControl
          label={__("Show Block shadow", "customizer-block-cf7")}
          checked={show_box_shadow}
          onChange={(show_box_shadow) => setAttributes({ show_box_shadow })}
          className="cfcf7-toggle-spacer"
        />

        {show_box_shadow && (
          <div>
            <RangeControl
              label={__("Horizontal Offset", "customizer-block-cf7")}
              value={block_h_offset}
              onChange={(block_h_offset) => setAttributes({ block_h_offset })}
              step={0.1}
              min={-2}
              max={2}
            />

            <RangeControl
              label={__("Vertical Offset", "customizer-block-cf7")}
              value={block_v_offset}
              onChange={(block_v_offset) => setAttributes({ block_v_offset })}
              step={0.1}
              min={-3}
              max={3}
            />

            <RangeControl
              label={__("Blur", "customizer-block-cf7")}
              value={block_blur}
              onChange={(block_blur) => setAttributes({ block_blur })}
              step={0.1}
              min={0}
              max={15}
            />

            <RangeControl
              label={__("Spread", "customizer-block-cf7")}
              value={block_spread}
              onChange={(block_spread) => setAttributes({ block_spread })}
              step={0.1}
              min={-10}
              max={5}
            />

            <PanelColorSettings
              className="cfcf7-colorpanel-spacer"
              initialOpen={false}
              title={__("Shadow Color", "customizer-block-cf7")}
              colorSettings={[
                {
                  value: block_shadow_color,
                  onChange: (block_shadow_color) =>
                    setAttributes({ block_shadow_color }),
                },
              ]}
            />
          </div>
        )}
      </div>
    </PanelBody>
  );
};

export default BlockShadow;
