import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import { PanelBody, ToggleControl, RangeControl } from "@wordpress/components";
import { PanelColorSettings } from "@wordpress/block-editor";

export default class SubmitShadow extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        show_submit_shadow,
        submit_h_offset,
        submit_v_offset,
        submit_blur,
        submit_spread,
        submit_shadow_color,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Submit Shadow", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-submit-shadow'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-submit-shadow')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <ToggleControl
            label={__("Show Box Shadow", "customizer-block-cf7")}
            checked={show_submit_shadow}
            onChange={(show_submit_shadow) =>
              setAttributes({ show_submit_shadow })
            }
            className="cfcf7-toggle-spacer"
          />

          {show_submit_shadow && (
            <div>
              <RangeControl
                label={__("Horizontal Offset", "customizer-block-cf7")}
                value={submit_h_offset}
                onChange={(submit_h_offset) =>
                  setAttributes({ submit_h_offset })
                }
                step={0.1}
                min={-0.5}
                max={0.5}
              />

              <RangeControl
                label={__("Vertical Offset", "customizer-block-cf7")}
                value={submit_v_offset}
                onChange={(submit_v_offset) =>
                  setAttributes({ submit_v_offset })
                }
                step={0.1}
                min={-0.5}
                max={0.5}
              />

              <RangeControl
                label={__("Blur", "customizer-block-cf7")}
                value={submit_blur}
                onChange={(submit_blur) => setAttributes({ submit_blur })}
                step={0.1}
                min={-3}
                max={3}
              />

              <RangeControl
                label={__("Spread", "customizer-block-cf7")}
                value={submit_spread}
                onChange={(submit_spread) => setAttributes({ submit_spread })}
                step={0.1}
                min={-3}
                max={3}
              />

              <PanelColorSettings
                initialOpen={true}
                className="cfcf7-colorpanel-spacer"
                title={__("Shadow Color", "customizer-block-cf7")}
                colorSettings={[
                  {
                    value: submit_shadow_color,
                    onChange: (submit_shadow_color) =>
                      setAttributes({ submit_shadow_color }),
                    label: "",
                  },
                ]}
              />
            </div>
          )}
        </div>
      </PanelBody>
    );
  }
}
