import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import { PanelBody, ToggleControl, RangeControl } from "@wordpress/components";
import { PanelColorSettings } from "@wordpress/block-editor";

export default class SubmitBorder extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        show_submit_border,
        submit_border_radius,
        submit_border_width,
        submit_border_color,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;
    const SIZE_MAX = 3;

    return (
      <PanelBody
        title={__("Submit Border", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-submit-border'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-submit-border')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <RangeControl
            label={__("Border Radius", "customizer-block-cf7")}
            value={submit_border_radius}
            onChange={(submit_border_radius) =>
              setAttributes({ submit_border_radius })
            }
            step={SIZE_STEP}
            min={0}
            max={SIZE_MAX}
          />

          <ToggleControl
            label={__("Show Submit Border", "customizer-block-cf7")}
            checked={show_submit_border}
            onChange={(show_submit_border) =>
              setAttributes({ show_submit_border })
            }
            className="cfcf7-toggle-spacer"
          />

          {show_submit_border && (
            <div>
              <RangeControl
                label={__("Border Width", "customizer-block-cf7")}
                step={1}
                min={0}
                max={5}
                withInputSubmit={true}
                isShiftStepEnabled
                value={submit_border_width}
                onChange={(submit_border_width) =>
                  setAttributes({ submit_border_width })
                }
              />

              <PanelColorSettings
                title={__("Border Color", "customizer-block-cf7")}
                className="cfcf7-colorpanel-spacer"
                initialOpen={true}
                colorSettings={[
                  {
                    value: submit_border_color,
                    onChange: (submit_border_color) =>
                      setAttributes({ submit_border_color }),
                    label: "",
                  },
                ]}
              />
            </div>
          )}
        </div>
      </PanelBody>
    );
  }
}
