import { __ } from '@wordpress/i18n';
import { useHandleToggle } from './useHandleToggle'; // Import the custom hook

import { InspectorControls } from "@wordpress/block-editor";
import { PanelBody } from "@wordpress/components";
import { page, button, settings } from "@wordpress/icons";

/**
 * Import component files
 */

import BlockBackground from "./blockbackground";
import BlockSizing from "./blocksizing";
import BlockMargin from "./blockmargin";
import BlockPadding from "./blockpadding";
import BlockBorder from "./blockborder";
import BlockShadow from "./blockshadow";

import FieldBackground from "./fieldbackground";
import FieldLabels from "./fieldlabels";
import FieldText from "./fieldtext";
import FieldPadding from "./fieldpadding";
import FieldBorder from "./fieldborder";
import FieldShadow from "./fieldshadow";

import SubmitBackground from "./submitbackground";
import SubmitText from "./submittext";
import SubmitWidth from "./submitwidth";
import SubmitPadding from "./submitpadding";
import SubmitBorder from "./submitborder";
import SubmitShadow from "./submitshadow";


/**
 * Functional Component with useState
 */
const Inspector = ({ attributes, setAttributes }) => {
  // Create state to track which parent and child panels are open
  const {
    openParentPanel,
    handleParentToggle,
    openChildPanel,
    handleChildToggle, // Use the custom hook to handle child and parent panel toggles
  } = useHandleToggle();

  return (
    <InspectorControls>
      <PanelBody
        title={__("Block Settings", "customizer-block-cf7")}
        className="cfcf7-parent-panel"
        icon={settings}
        initialOpen={false}
        scrollAfterOpen={true}
        opened={openParentPanel === 'panel-block-settings'} // Check if this panel is open
        onToggle={() => handleParentToggle('panel-block-settings')}

      >
        <BlockSizing 
        setAttributes={setAttributes}
        attributes={attributes}
        openChildPanel={openChildPanel}
        handleChildToggle={handleChildToggle} 
           />
        <BlockMargin 
        setAttributes={setAttributes}
        attributes={attributes}
        openChildPanel={openChildPanel}
        handleChildToggle={handleChildToggle} 
        />
        <BlockPadding 
         setAttributes={setAttributes}
         attributes={attributes}
         openChildPanel={openChildPanel}
         handleChildToggle={handleChildToggle} 
        />
        <BlockBackground
          setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle} 
        />
        
        <BlockBorder   setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />
        
        <BlockShadow
          setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle} 
        />
      </PanelBody>

      <PanelBody
        title={__("Form Fields", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-parent-panel"
        icon={page}
        scrollAfterOpen={true}
        opened={openParentPanel === 'panel-form-fields'} // Check if this panel is open
        onToggle={() => handleParentToggle('panel-form-fields')}
      >
        <FieldBackground   
          setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

        <FieldLabels  setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

        <FieldText setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}   />

        <FieldPadding setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}   />

        <FieldBorder setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

        <FieldShadow setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />
          
      </PanelBody>

      <PanelBody
          title={__("Submit Button", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-parent-panel"
          icon={button}
          scrollAfterOpen={true}
          opened={openParentPanel === 'panel-submit-button'} // Check if this panel is open
          onToggle={() => handleParentToggle('panel-submit-button')}
          >
          <SubmitBackground setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

          <SubmitText setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

          <SubmitWidth setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

          <SubmitPadding setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

          <SubmitBorder setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />

          <SubmitShadow setAttributes={setAttributes}
          attributes={attributes}
          openChildPanel={openChildPanel}
          handleChildToggle={handleChildToggle}  />
          
        </PanelBody>
    </InspectorControls>
  );
};

export default Inspector;
