import { useState } from 'react';

export const useHandleToggle = () => {
  // Separate state for parent panels and child panels
  const [openParentPanel, setOpenParentPanel] = useState(null);
  const [openChildPanel, setOpenChildPanel] = useState(null);

  // Function to handle parent panel toggling
  const handleParentToggle = (panel) => {
    if (openParentPanel === panel) {
      setOpenParentPanel(null); // Close the parent panel if it is already open
    } else {
      setOpenParentPanel(panel); // Open the clicked parent panel
    }
  };

  // Function to handle child panel toggling
  const handleChildToggle = (panel) => {
    if (openChildPanel === panel) {
      setOpenChildPanel(null); // Close the child panel if it is already open
    } else {
      setOpenChildPanel(panel); // Open the clicked child panel
    }
  };

  return {
    openParentPanel,
    handleParentToggle,
    openChildPanel,
    handleChildToggle,
  };
};
