import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import {
  PanelBody,
  RangeControl,
  SelectControl
} from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class FieldLabels extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        label_color,
        label_font_size,
        label_line_height,
        label_case,
        label_font_weight,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const SIZE_STEP = 0.1;
    const SIZE_MAX = 3;

    return (
      <PanelBody
        title={__("Field Labels", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-labels'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-labels')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">

          <RangeControl
            label={__("Font Size", "customizer-block-cf7")}
            value={label_font_size}
            onChange={(label_font_size) => setAttributes({ label_font_size })}
            step={SIZE_STEP}
            min={0.8}
            max={1.6}
          />

          <RangeControl
            label={__("Line Height", "customizer-block-cf7")}
            value={label_line_height}
            onChange={(label_line_height) =>
              setAttributes({ label_line_height })
            }
            step={SIZE_STEP}
            min={0.5}
            max={3}
          />

          <SelectControl
            className="cfcf7-selectcontrol-spacer"
            label={__("Text Transform", "customizer-block-cf7")}
            value={label_case}
            options={[
              { label: __("None", "customizer-block-cf7"), value: "" },
              { label: __("Uppercase", "customizer-block-cf7"), value: "uppercase" },
              { label: __("Lowercase", "customizer-block-cf7"), value: "lowercase" },
              { label: __("Capitalize", "customizer-block-cf7"), value: "capitalize" }
            ]}
            onChange={(newLabelCase) => setAttributes({ label_case: newLabelCase })}
          />

          <SelectControl
            className="cfcf7-selectcontrol-spacer"
            label={__("Font Weight", "customizer-block-cf7")}
            value={label_font_weight}
            options={[
              { label: __("Light (300)", "customizer-block-cf7"), value: "300" },
              { label: __("Normal (400)", "customizer-block-cf7"), value: "400" },
              { label: __("Bold (700)", "customizer-block-cf7"), value: "700" },
            ]}
            onChange={(newFontWeight) => setAttributes({ label_font_weight: newFontWeight })}
          />

          <PanelColorSettings
            className="cfcf7-colorpanel-spacer"
            title={__("Font Color", "customizer-block-cf7")}
            colorSettings={[
              {
                value: label_color,
                onChange: (label_color) => setAttributes({ label_color }),
                label: "",
              },
            ]}
          />
        </div>
      </PanelBody>
    );
  }
}
