import { __ } from '@wordpress/i18n';

export function useFormData(props) {
    // Fetch form data from the global object
    const formData = window.cfcf7.contactForms;

    // Initialize form options with the default unselected option
    const formOptions = [
        { value: "unselected", label: __("Select a contact form", "customizer-block-cf7") },
    ];

    // Handle the case where no forms are available
    if (!formData || formData.length === 0) {
        // No forms found, set attributes to reflect this
        props.setAttributes({ form_selector_toggle: false, contact_form: "empty" });
      //  console.log('No contact forms available.');
        return { formOptions, selectForm }; // Return early with just the default option
    }

    // Populate the form options with available forms
    formData.forEach(form => {
        formOptions.push({
            value: form.id,
            label: form.title,
            success_message: form.success_message,
        });
    });

    // Function to handle form selection
    function selectForm(selectedFormId) {
        if (selectedFormId === "unselected") {
            props.setAttributes({ contact_form: selectedFormId, form_selector_toggle: true });
        } else {
            props.setAttributes({ contact_form: selectedFormId, form_selector_toggle: false });
        }
    }

    // Return the form options and selection handler
    return { formOptions, selectForm };
}
