import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import PreviewIconDesktop from "./previewicondesktop";
import PreviewIconTablet from "./previewicontablet";
import PreviewIconMobile from "./previewiconmobile";

import { RangeControl, PanelBody } from "@wordpress/components";

export default class BlockSizing extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: { 
        base_font_size,
        base_font_size_tablet,
        base_font_size_mobile
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    const isPrewiewMode = this.props.attributes.previewmode;

    // Desktop Preview Mode
    if (isPrewiewMode === "Desktop") {
      return (
        <PanelBody
          title={__("Block Sizing", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-sizing-desktop'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-sizing-desktop')} // Handle child panel toggle
        >
          <PreviewIconDesktop />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Base Font Size", "customizer-block-cf7")}
              value={base_font_size}
              onChange={(base_font_size) => setAttributes({ base_font_size })}
              step={1}
              min={14}
              max={20}
              help={__("Scale everything by adjusting the base font size.", "customizer-block-cf7")}
            />
          </div>
        </PanelBody>
      );
    }
    // Tablet Preview Mode
    else if (isPrewiewMode === "Tablet") {
      return (
        <PanelBody
          title={__("Block Sizing", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-sizing-tablet'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-sizing-tablet')} // Handle child panel toggle
        >
          <PreviewIconTablet />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Base Font Size", "customizer-block-cf7")}
              value={base_font_size_tablet}
              onChange={(base_font_size_tablet) => setAttributes({ base_font_size_tablet })}
              step={1}
              min={14}
              max={20}
              help={__("Scale everything by adjusting the base font size.", "customizer-block-cf7")}
            />
          </div>
        </PanelBody>
      );
    }
    // Mobile Preview Mode
    else {
      return (
        <PanelBody
          title={__("Block Sizing", "customizer-block-cf7")}
          initialOpen={false}
          className="cfcf7-child-panel"
          scrollAfterOpen={true}
          opened={openChildPanel === 'panel-block-sizing-mobile'} // Check if this panel is open
          onToggle={() => handleChildToggle('panel-block-sizing-mobile')} // Handle child panel toggle
        >
          <PreviewIconMobile />
          <div className="cfcf7-panel-spacer">
            <RangeControl
              label={__("Base Font Size", "customizer-block-cf7")}
              value={base_font_size_mobile}
              onChange={(base_font_size_mobile) => setAttributes({ base_font_size_mobile })}
              step={1}
              min={13}
              max={19}
              help={__("Scale everything by adjusting the base font size.", "customizer-block-cf7")}
            />
          </div>
        </PanelBody>
      );
    }
  }
}
