import { __ } from '@wordpress/i18n';
const { Component } = wp.element;
import {
  PanelBody,
  ToggleControl,
  RangeControl,
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from "@wordpress/components";

import { PanelColorSettings } from "@wordpress/block-editor";

export default class FieldShadow extends Component {
  constructor() {
    super(...arguments);
  }

  render() {
    const {
      attributes: {
        show_field_box_shadow,
        field_h_offset,
        field_v_offset,
        field_blur,
        field_spread,
        field_shadow_color,
        field_shadow_position,
      },
      setAttributes,
      openChildPanel, // Receive openChildPanel state
      handleChildToggle, // Receive handleChildToggle function
    } = this.props;

    return (
      <PanelBody
        title={__("Field Shadow", "customizer-block-cf7")}
        initialOpen={false}
        className="cfcf7-child-panel"
        scrollAfterOpen={true}
        opened={openChildPanel === 'panel-field-shadow'} // Check if this panel is open
        onToggle={() => handleChildToggle('panel-field-shadow')} // Handle child panel toggle
      >
        <div className="cfcf7-panel-spacer">
          <ToggleControl
            className="cfcf7-toggle-spacer"
            label={__("Show Field Shadow", "customizer-block-cf7")}
            checked={show_field_box_shadow}
            onChange={(show_field_box_shadow) =>
              setAttributes({ show_field_box_shadow })
            }
          />

          {show_field_box_shadow && (
            <div>
              <ToggleGroupControl
                label={__("Shadow Position", "customizer-block-cf7")}
                className="cfcf7-togglegroup-spacer"
                value={field_shadow_position}
                isBlock
                onChange={(field_shadow_position) => {
                  setAttributes({ field_shadow_position });
                }}
              >
                <ToggleGroupControlOption value="inset" label="Inner" />
                <ToggleGroupControlOption value="outset" label="Outer" />
              </ToggleGroupControl>

              <RangeControl
                label={__("Horizontal Offset", "customizer-block-cf7")}
                value={field_h_offset}
                onChange={(field_h_offset) => setAttributes({ field_h_offset })}
                step={0.1}
                min={-0.3}
                max={0.3}
              />

              <RangeControl
                label={__("Vertical Offset", "customizer-block-cf7")}
                value={field_v_offset}
                onChange={(field_v_offset) => setAttributes({ field_v_offset })}
                step={0.1}
                min={-0.1}
                max={0.1}
              />

              <RangeControl
                label={__("Blur", "customizer-block-cf7")}
                value={field_blur}
                onChange={(field_blur) => setAttributes({ field_blur })}
                step={0.1}
                min={0}
                max={2}
              />

              <RangeControl
                label={__("Spread", "customizer-block-cf7")}
                value={field_spread}
                onChange={(field_spread) => setAttributes({ field_spread })}
                step={0.1}
                min={-2}
                max={0.2}
              />

              <PanelColorSettings
                initialOpen={true}
                className="cfcf7-colorpanel-spacer"
                title={__("Shadow Color", "customizer-block-cf7")}
                colorSettings={[
                  {
                    value: field_shadow_color,
                    onChange: (field_shadow_color) =>
                      setAttributes({ field_shadow_color }),
                    label: "",
                  },
                ]}
              />
            </div>
          )}
        </div>
      </PanelBody>
    );
  }
}
